body {
  margin: 0;
  padding: 0;
}

.wrapper {
  background-color: #b4eeaf;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.clock {
  background-color: #a8efef;
  border: 5px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vh;
  height: 80vh;
  display: flex;
  position: relative;
}

.clock__center {
  z-index: 3;
  background-color: #efc405;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.clock__hour {
  transform-origin: bottom;
  z-index: 1;
  background-color: #1b7ad4;
  width: 20px;
  height: 20vh;
  animation: 48s linear infinite rotate-arrow;
  position: absolute;
  bottom: 50%;
  transform: rotate(90deg);
}

.clock__minute {
  transform-origin: bottom;
  z-index: 2;
  background-color: #3dd41b;
  width: 10px;
  height: 40vh;
  animation: 4s linear infinite rotate-arrow;
  position: absolute;
  bottom: 50%;
  transform: rotate(0);
}

@keyframes rotate-arrow {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

